.search-bar {
    margin: 20px;
    text-align: center;
}

.search-bar input {
    width: 50%;
    margin: 5px;
}

.glyphicon-search {
    padding: 3px;
}

.video-item img {
    max-width: 64px;
}

.video-detail .details {
    margin-top: 10px;
    padding: 10px;
    border: 1px solid #ddd;
}

.list-group-item {
    cursor: pointer;
}

.list-group-item:hover {
    background-color: #eee;
}



